.button {
  display: inline-block;
  padding: 10px 25px;
  border-radius: 5px;
  background-color: transparent;
  text-align: center;
  text-transform: uppercase;
  font-family: "roboto-condensed", "Arial", sans-serif;
  color: #ffffff;
  font-size: 16px;
  line-height: 20px;
  cursor: pointer; }
  .button--action {
    background-color: #00b2ff;
    color: #ffffff;
    letter-spacing: 0.05em;
    border: none;
    white-space: nowrap;
    padding: 18px 50px;
    font-size: 16px;
    line-height: 19px; }
    .button--action:hover {
      opacity: 0.9; }
    .button--action:focus {
      background-color: #ebebeb; }
    .button--action:active {
      opacity: 0.7; }
    @media (max-width: 1024px) {
      .button--action {
        padding: 18px 15px; } }
  .button--callback {
    border-color: #ffffff;
    border: 1px solid #ffffff; }
    .button--callback:hover {
      opacity: 0.9; }
    .button--callback:focus {
      opacity: 0.9; }
    .button--callback:active {
      opacity: 0.7; }

.social {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  min-width: 130px;
  flex-wrap: wrap; }

.social__link svg {
  fill: #444444;
  vertical-align: middle; }

.social__link:hover svg,
.social__link:focus svg {
  fill: #68b738; }

.social__link:active svg {
  fill: #68b738;
  opacity: 0.3; }

@media (min-width: 1024px) {
  .social__link--vk svg {
    height: 16px;
    width: 30px; }
  .social__link--insta svg {
    height: 23px;
    width: 23px; }
  .social__link--facebook svg {
    height: 28px;
    width: 14px; } }

.social__item {
  padding: 0 15px; }

.links {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  min-height: 30px; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  white-space: nowrap;
  border: 0;
  clip-path: inset(100%); }

body {
  box-sizing: border-box;
  font-family: "PT-Mono", "Arial", sans-serif;
  overflow-x: hidden; }

*,
*::before,
*::after {
  box-sizing: inherit; }
